<template>
  <div class="goodsEach">
    <a class="goods_eve" href="#" target="_blank" v-for="(item,index) in getGoodsList[key_name_now]">
      <img :src="item.pic" alt>
      <span>{{item.name}}</span>
    </a>
  </div>
</template>


<script>
export default {
    name: "songshuGoodsListClassifyRightEach",
    data(){
      return{
        key_name_now:"nut",
      }
    },
    props: ["getGoodsList"],
    created() {
      this.Observer.$on("getKeyName", req => {
        this.key_name_now=req;
      });
  }
};
</script>

<style scoped>
.goodsEach {
  flex-wrap: wrap;
  flex-shrink: 1;
  display: flex;
}
</style>