<template>
  <aside>
    <div :class="{active:currentName==item.name}" @click="changeColor(item,index)" v-for="(item,index) in foodsClass">{{item.name}}</div>
  </aside>
</template>

<script>
export default {
    name:"songshuGoodsListClassifyLeft",
    data(){
        return{
            foodsClass:[
                {name:"坚果炒货",key_name:"nut"},
                {name:"果干蜜饯",key_name:"dried"},
                {name:"肉类熟食",key_name:"meat"},
                {name:"饼干鹏华",key_name:"cookies"},
                {name:"糕点点心",key_name:"pastry"},
                {name:"素食卤味",key_name:"braised"},
                {name:"冲泡茶饮",key_name:"tea"},
                {name:"海味河鲜",key_name:"seafood"},
                {name:"松鼠新品",key_name:"newfood"},
                {name:"力和礼品",key_name:"gift"},
                ],
           currentName:"坚果炒货",
        }
    },
    created(){
        
    },
    methods:{
        changeColor(item,index){
            this.currentName=item.name;
            if(index==1){
                $("aside").scrollTop(146);
            }
            if(index>1){
                $("aside").scrollTop(292);
            }
            this.Observer.$emit("getKeyName",item.key_name);
        }
    }
}
</script>

